      { skill: "BootStrap", percent: 90 },
      { skill: "Tailwind", percent: 75 },
      { skill: "Redux-tollkit", percent: 90 },
    ]);
    const form = reactive({
      email: "",
      name: "",
      message: "",
      phone: "",
    });
    const rules = computed(() => {
      return {
        email: { required, email },
        phone: { required, minLength: minLength(0, 9) },
        name: { required, minLength: minLength(0, 50) },
        message: { required, minLength: minLength(0, 10000) },
      };
    });
    const v$ = useValidate(rules, form);
    return {
      beforeEnter2,
      navArrow,
      skills,
      enter2,
      beforeEnter,
      enter,
      afterEnter,
      form,
      v$,
      activeTab,
    };
  },
  methods: {
    onScroll() {
      const sections = document.querySelectorAll("section");
      const sectionsPos = {};
      sections.forEach((section) => {
        sectionsPos[section.id] = section.offsetTop;
      });
      var scrollPos =
        document.documentElement.scrollTop || document.body.scrollTop;
      for (var id in sectionsPos) {
        if (sectionsPos[id] <= scrollPos + 200) {
          document.querySelector(".active").classList.remove("active");
          document
            .querySelector(`.scroll_spy li a[href*=${id}]`)
            .classList.add("active");
        }
      }
    },
    toggleSideBar() {
      window.scroll(0, 0);
      this.navArrow = !this.navArrow;
    },
    async sendEmail() {
      await emailjs
        .sendForm(
          "service_snojy0s",
          "template_p83ow3t",
          this.$refs.form,
          "E3gerAVqUMCLpFVG2"
        )
        .then(
          (result) => {
            console.log(result);
            console.log(this.$refs.form);
          },
          (error) => {
            console.log(error.text);
          }
        );
      // }
    },
  },
  mounted() {
    window.addEventListener("scroll", this.onScroll);
  },
};
</script>
